import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import IconButton from "@mui/material/IconButton";
import InputBase from "@mui/material/InputBase";
import SearchIcon from "@mui/icons-material/Search";
import Box from "@mui/material/Box";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
import MenuItem from "@mui/material/MenuItem";
import CircleIcon from "@mui/icons-material/Circle";
import avatar from "icons/avatar.svg";
import Tooltip from "@mui/material/Tooltip";
import Avatar from "@mui/material/Avatar";
import { useState } from "react";
import Divider from "@mui/material/Divider";
import ListItemIcon from "@mui/material/ListItemIcon";
import { Logout, PersonAdd, Settings } from "@mui/icons-material";
import Menu from "@mui/material/Menu";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Radio from "@mui/material/Radio";
import store, { Role } from "stores/account-store";
import { observer } from "mobx-react-lite";
import { Link } from "react-router-dom";

const UserMenu = observer(
  ({
    anchorEl,
    open,
    handleClose,
  }: {
    anchorEl: Element | undefined;
    open: boolean;
    handleClose: () => void;
  }) => {
    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
      store.setRole((event.target as HTMLInputElement).value as Role);
    };

    if (!anchorEl) {
      return null;
    }

    return (
      <Menu
        anchorEl={anchorEl}
        id="account-menu"
        open={open}
        onClose={handleClose}
        PaperProps={{ elevation: 10 }}
        transformOrigin={{ horizontal: "right", vertical: "top" }}
        anchorOrigin={{ horizontal: "right", vertical: "bottom" }}
      >
        <FormControl sx={{ p: 2, pt: 1 }}>
          <FormLabel id="role-label">Роль</FormLabel>
          <RadioGroup
            aria-labelledby="role-label"
            value={store.role}
            onChange={handleChange}
            name="radio-buttons-group"
          >
            <FormControlLabel
              value={"volunteer" as Role}
              control={<Radio />}
              label="Волонтер"
            />
            <FormControlLabel
              value={"organizer" as Role}
              control={<Radio />}
              label="Организатор"
            />
            <FormControlLabel
              value={"sponsor" as Role}
              control={<Radio />}
              label="Спонсор"
            />
          </RadioGroup>
        </FormControl>
        <Divider />
        <MenuItem>
          <ListItemIcon>
            <PersonAdd fontSize="small" />
          </ListItemIcon>
          Пригласить друга
        </MenuItem>
        <MenuItem>
          <ListItemIcon>
            <Settings fontSize="small" />
          </ListItemIcon>
          Настройки
        </MenuItem>
        <MenuItem>
          <ListItemIcon>
            <Logout fontSize="small" />
          </ListItemIcon>
          Выход
        </MenuItem>
      </Menu>
    );
  }
);

const AppHeader = () => {
  const [anchorEl, setAnchorEl] = useState<HTMLElement | undefined>();
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(undefined);
  };

  return (
    <>
      <Grid
        sx={{ p: 2 }}
        container
        spacing={2}
        justifyContent="space-between"
        alignItems="center"
        gap={10}
      >
        <Grid item>
          <MenuItem sx={{ display: "inline-flex", gap: 1 }}>
            <CircleIcon sx={{ fill: "#5A3DFF" }} />
            <Link to="/" style={{ textDecoration: "none" }}>
              <Typography
                variant="overline"
                sx={{ color: "#5A3DFF", fontSize: 16, fontWeight: 600 }}
              >
                Внимание
              </Typography>
            </Link>
          </MenuItem>
        </Grid>
        <Grid item>
          <Link to="/events" style={{ textDecoration: "none", color: "black" }}>
            <Typography sx={{ fontWeight: 600 }}>Актуальные события</Typography>
          </Link>
        </Grid>
        <Grid item flexGrow={1}>
          <Box
            component="form"
            sx={{
              p: "2px 4px",
              display: "flex",
              alignItems: "center",
              backgroundColor: "#f3f5f7",
              borderRadius: 5,
            }}
          >
            <IconButton sx={{ p: "10px" }} aria-label="search">
              <SearchIcon />
            </IconButton>
            <InputBase
              sx={{ ml: 1, flex: 1 }}
              placeholder="Поиск доброго дела"
              inputProps={{ "aria-label": "Поиск доброго дела" }}
            />
          </Box>
        </Grid>
        <Grid item>
          <MenuItem sx={{ display: "inline-flex", gap: 1 }}>
            <FavoriteBorderIcon sx={{ fill: "#5A3DFF" }} />
            <Typography sx={{ color: "#5A3DFF", fontWeight: 600 }}>
              Стать спонсором
            </Typography>
          </MenuItem>
        </Grid>
        <Grid item>
          <Tooltip title="Аккаунт">
            <IconButton
              onClick={handleClick}
              size="small"
              aria-controls={open ? "account-menu" : undefined}
              aria-haspopup="true"
              aria-expanded={open ? "true" : undefined}
            >
              <Avatar src={avatar} sx={{ width: 48, height: 48 }} />
            </IconButton>
          </Tooltip>
        </Grid>
      </Grid>
      <UserMenu anchorEl={anchorEl} open={open} handleClose={handleClose} />
    </>
  );
};

export default AppHeader;
