import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import { observer } from "mobx-react-lite";
import cardImg from "images/cardImg.png";
import { useNavigate, useParams } from "react-router-dom";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Checkbox from "@mui/material/Checkbox";
import Button from "@mui/material/Button";
import AvatarGroup from "@mui/material/AvatarGroup";
import Avatar from "@mui/material/Avatar";
import Tooltip from "@mui/material/Tooltip";
import avatar from "icons/avatar.svg";
import face from "images/face.png";
import map from "images/map.png";

const EventPage = observer(() => {
  const navigate = useNavigate();
  const params = useParams();

  const handleAccept = () => {
    navigate(`/events/${params.id}/success`);
  };

  return (
    <Container maxWidth="xl">
      <Grid container>
        <Grid item xs={8}>
          <Typography variant="h2" gutterBottom>
            Субботник в Парке культуры
          </Typography>
          <img src={cardImg} alt="street" style={{ paddingBottom: 20 }} />
          <Typography color="text.secondary">Тематика</Typography>
          <Typography>Экологическое волонтерство</Typography>
          <Typography color="text.secondary">Что нужно делать</Typography>
          <ul style={{ margin: 0, paddingLeft: 15 }}>
            <li>
              <Typography>уборка территории</Typography>
            </li>
            <li>
              <Typography>покраска бордюров</Typography>
            </li>
          </ul>
          <Typography color="text.secondary">Когда</Typography>
          <Typography>27 июня, 18.00</Typography>
          <Typography color="text.secondary">Где</Typography>
          <Typography>Москва, Садовая улица, дом 15</Typography>
          {/* <Box width={600} height={250} sx={{ backgroundColor: "gray" }}></Box> */}
          <img src={map} height={500} alt="map" style={{ padding: 20 }} />
          <Typography color="text.secondary">Выдаем</Typography>
          <Typography>Экипировку</Typography>
          <Typography color="text.secondary">Что получите от работы</Typography>
          <ul style={{ margin: 0, paddingLeft: 15 }}>
            <li>
              <Typography>благодарственные письма</Typography>
            </li>
            <li>
              <Typography>
                возможность присоединиться к команде волонтеров
              </Typography>
            </li>
          </ul>
        </Grid>
        <Grid item xs={4}>
          <Box sx={{ borderRadius: "32px", backgroundColor: "#EFEFEF", p: 4 }}>
            <Typography color="text.secondary">Когда</Typography>
            <Typography>27 июня, 18.00</Typography>
            <Typography color="text.secondary">Где</Typography>
            <Typography>Москва, Садовая улица, дом 15</Typography>
            <Typography color="text.secondary">
              Возрастные ограничения
            </Typography>
            <Typography>14+</Typography>
            <Box
              sx={{
                borderWidth: 2,
                borderStyle: "solid",
                borderRadius: 5,
                p: 2,
                m: 1,
                borderColor: "#e3e3e3",
              }}
            >
              <Box display="flex" alignItems="center">
                <Checkbox sx={{ pl: 0 }} />
                <Typography>Я новичок</Typography>
              </Box>
              <Typography variant="caption">
                Вас будет курировать опытный волонтер, который все объяснит и
                покажет
              </Typography>
            </Box>
            <Box display="flex" justifyContent="center" padding={2}>
              <Button variant="contained" onClick={handleAccept}>
                Готов помочь
              </Button>
            </Box>
            <Box
              display="flex"
              alignItems="center"
              justifyContent="center"
              gap={1}
              sx={{ pt: 1 }}
            >
              <AvatarGroup spacing="medium">
                <Tooltip
                  children={<Avatar src={avatar} />}
                  title={"Иван Иванов"}
                ></Tooltip>
                <Tooltip
                  children={<Avatar src={avatar} />}
                  title={"Петр Петров"}
                ></Tooltip>
                <Tooltip
                  children={<Avatar src={avatar} />}
                  title={"Василий Васильев"}
                ></Tooltip>
                <Avatar>+5</Avatar>
              </AvatarGroup>
              <Typography variant="caption">Участвуют N человек</Typography>
            </Box>
          </Box>
          <Box
            sx={{
              borderRadius: "32px",
              backgroundColor: "#EFEFEF",
              p: 2,
              pl: 4,
              mt: 2,
            }}
          >
            <Grid container gap={2} alignItems="center">
              <Grid item>
                <Avatar src={face} alt="face" />
              </Grid>
              <Grid item>
                <Typography>Организатор события</Typography>
                <Typography color="text.secondary">Александр Иванов</Typography>
              </Grid>
            </Grid>
          </Box>
          <Box
            sx={{
              borderRadius: "32px",
              borderWidth: 2,
              borderStyle: "solid",
              borderColor: "#e3e3e3",
              p: 2,
              pl: 4,
              pr: 4,
              mt: 2,
            }}
          >
            <Typography>Сохранить брошюру мероприятия</Typography>
            <Typography gutterBottom variant="caption" color="text.secondary">
              PDF брошюру можно распечатать и поделиться с тем, кто хочет стать
              волонтером
            </Typography>
            <Box display="flex" justifyContent="center" sx={{ pt: 2 }}>
              <Button variant="contained">Распечатать брошюру</Button>
            </Box>
          </Box>
        </Grid>
      </Grid>
      <Box height={100}></Box>
    </Container>
  );
});

export default EventPage;
