import Box from "@mui/material/Box";
import Checkbox from "@mui/material/Checkbox";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormGroup from "@mui/material/FormGroup";
import Typography from "@mui/material/Typography";
import lamp from "images/lamp.png";
import { observer } from "mobx-react-lite";
import onboardingStore from "stores/onboarding-store";
import TextField from "@mui/material/TextField";
import { useEffect } from "react";
import Button from "@mui/material/Button";
import { Link } from "react-router-dom";
import Container from "@mui/material/Container";

const Onboarding = observer(() => {
  useEffect(() => {
    onboardingStore.init();
    return () => onboardingStore.reset();
  }, []);

  return (
    <Container>
      {onboardingStore.currentQuestionIndex !== 0 && (
        <Typography
          sx={{ textDecoration: "none", color: "grey", cursor: "pointer" }}
          variant="caption"
          onClick={() => onboardingStore.goPrevQuestion()}
        >
          {"< К предыдущему шагу"}
        </Typography>
      )}
      <Box sx={{ pt: 1, pb: 1 }}>
        <img src={lamp} alt="Лампа" />
      </Box>
      <Typography variant="caption">
        Шаг {onboardingStore.currentQuestionIndex + 1} из{" "}
        {onboardingStore.values?.length}
      </Typography>
      <Typography variant="h4">
        {onboardingStore.currentQuestion?.title}
      </Typography>
      <FormGroup>
        {onboardingStore.currentQuestion?.answers.map((x) => (
          <Box key={x.title}>
            {x.type === "checkbox" ? (
              <FormControlLabel
                control={
                  <Checkbox
                    checked={x.value}
                    onChange={() => (x.value = !x.value)}
                  />
                }
                label={x.title}
              />
            ) : (
              <Box paddingTop={2}>
                <Typography gutterBottom>{x.title}</Typography>
                <FormControlLabel
                  control={
                    <>
                      <TextField
                        label="От"
                        type="date"
                        value={x.value.from}
                        onChange={(e) => (x.value.from = e.target.value)}
                        sx={{ width: 220, m: 1 }}
                      />
                      <TextField
                        label="До"
                        type="date"
                        value={x.value.to}
                        onChange={(e) => (x.value.to = e.target.value)}
                        sx={{ width: 220, m: 1 }}
                      />
                    </>
                  }
                  label={undefined}
                />
              </Box>
            )}
          </Box>
        ))}
      </FormGroup>
      {!onboardingStore.isLastQuestion && (
        <Button
          variant="contained"
          onClick={() => onboardingStore.goNextQuestion()}
        >
          Далее
        </Button>
      )}
      {onboardingStore.isLastQuestion && (
        <Link to="/events">
          <Button variant="contained">Завершить</Button>
        </Link>
      )}
    </Container>
  );
});

export default Onboarding;
