import { makeAutoObservable } from "mobx";

type Question = {
  title: string;
  answers: { title: string; type: "checkbox" | "dateRange"; value: any }[];
};

const questionStub: Question[] = [
  {
    title: "Какую помощь вы готовы оказывать?",
    answers: [
      {
        title: "Хочу помочь экологии города",
        type: "checkbox",
        value: false,
      },
      { title: "Хочу помогать животным", type: "checkbox", value: false },
      {
        title: "Хочу помогать пенсионерам",
        type: "checkbox",
        value: false,
      },
      {
        title: "Хочу помогать на культурных проектах",
        type: "checkbox",
        value: false,
      },
      { title: "Другое", type: "checkbox", value: false },
    ],
  },
  {
    title: "Сколько вам лет?",
    answers: [
      { title: "18 и меньше", type: "checkbox", value: false },
      { title: "от 18 до 45", type: "checkbox", value: false },
      { title: "45+", type: "checkbox", value: false },
    ],
  },
  {
    title: "Где вам удобнее оказать помощь?",
    answers: [
      { title: "ЦАО", type: "checkbox", value: false },
      { title: "ЮВАО", type: "checkbox", value: false },
      { title: "САО", type: "checkbox", value: false },
    ],
  },
  {
    title: "Когда вы хотите помогать?",
    answers: [
      {
        title: "Выбрать на календаре",
        type: "dateRange",
        value: {from: "2022-06-14", to: "2022-06-15"},
      },
    ],
  },
];

class OnboardingStore {
  constructor() {
    makeAutoObservable(this);
  }

  values: Question[] | undefined;

  currentQuestionIndex = 0;

  goNextQuestion() {
    this.currentQuestionIndex++;
  }

  goPrevQuestion() {
    this.currentQuestionIndex--;
  }

  get currentQuestion() {
    if (!this.values) {
      return undefined;
    }

    return this.values[this.currentQuestionIndex];
  }

  get isLastQuestion() {
    if (!this.values) {
      return undefined;
    }

    return this.values.length === this.currentQuestionIndex + 1;
  }

  ///@ TODO возвращать вопросы с backend
  async loadQuestions(): Promise<Question[]> {
    return new Promise((resolve) => {
      resolve(questionStub);
    });
  }

  async init() {
    this.values = await this.loadQuestions();
  }

  reset() {
    this.values = undefined;
    this.currentQuestionIndex = 0;
  }
}

const onboardingStore = new OnboardingStore();

export default onboardingStore;
