import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import human from "images/human.png";
import book from "images/book.png";
import { Link } from "react-router-dom";
import Container from "@mui/material/Container";
import { YMaps, Map, Placemark } from "react-yandex-maps";
import { useEffect } from "react";
import eventsStore from "stores/events-store";
import { observer } from "mobx-react-lite";

type CategoryType = {
  id: string;
  name: string;
};

const categories: CategoryType[] = [
  { id: "subject_1", name: "Найти пропавших" },
  { id: "subject_2", name: "Позаботиться о природе" },
  { id: "subject_3", name: "Помочь животным" },
  { id: "subject_4", name: "Помочь больным" },
  { id: "subject_5", name: "Работа в поликлиниках" },
  { id: "subject_6", name: "Сделать город чище" },
  { id: "subject_7", name: "Популяризировать спорт" },
  { id: "subject_8", name: "Внимание детям" },
];

const Main = observer(() => {
  useEffect(() => {
    eventsStore.loadEvents();
  }, []);

  const handleCategoryClick = (category: CategoryType) => {
    if (!eventsStore.isActiveFilter(category.id)) {
      eventsStore.addFilter(category.id);
    } else {
      eventsStore.removeFilter(category.id);
    }
  };

  return (
    <Container>
      <Box display="flex" flexDirection="column" alignItems="center">
        <img src={human} alt="Человек с котом" width={300} />
        <Typography variant="h2" textAlign="center" gutterBottom>
          <span style={{ color: "#5A3DFF" }}>13895</span> волонтерских заданий в
          Москве
        </Typography>
        <Typography gutterBottom>
          Делать добрые дела стало ещё проще. Выполняйте волонтерские задания и
          получайте бонусы
        </Typography>
        <Stack
          direction="row"
          justifyContent="center"
          alignItems="center"
          spacing={2}
          flexWrap="wrap"
          gap={1}
          padding={2}
        >
          <Button variant="contained">Хочу помочь</Button>
          <Button variant="contained">Стать организатором</Button>
        </Stack>
        <Typography variant="h3" sx={{ paddingTop: 5 }}>
          Добрые дела рядом с вами
        </Typography>
        <Stack
          direction="row"
          justifyContent="center"
          alignItems="center"
          spacing={2}
          flexWrap="wrap"
          gap={1}
          padding={2}
        >
          {categories.map((x, i) => (
            <Button
              key={i}
              variant={
                eventsStore.isActiveFilter(x.id) ? "contained" : "outlined"
              }
              sx={{ borderRadius: 10 }}
              onClick={() => handleCategoryClick(x)}
            >
              {x.name}
            </Button>
          ))}
        </Stack>
        {eventsStore.errorMessage && (
          <Typography>Произошла ошибка: {eventsStore.errorMessage}</Typography>
        )}
        {!eventsStore.errorMessage && (
          <YMaps>
            <Map
              width="100%"
              height={500}
              defaultState={{ center: [55.75, 37.57], zoom: 9 }}
              modules={["geoObject.addon.balloon", "geoObject.addon.hint"]}
            >
              {eventsStore.data
                ?.filter(
                  (x) =>
                    x.data.Props.MAP.VALUE &&
                    x.data.Props.MAP.VALUE.toString().includes(",")
                )
                .map((x) => (
                  <Placemark
                    key={x.id}
                    geometry={x.data.Props.MAP.VALUE?.toString()
                      .split(",")
                      .map((y) => {
                        return Number(y);
                      })}
                    properties={{
                      balloonContentHeader: `<font size=3><b><a target='_blank' href='events/${x.id}'>${x.data.Fields.NAME}</a></b></font>`,
                      balloonContentBody: `<p>${x.data.Props.DATE_START.VALUE}</p>`,
                      balloonContentFooter: `<font size=1>${x.data.Props.ADRESS.VALUE}</font>`,
                      hintContent: `${x.data.Fields.NAME}`,
                    }}
                  />
                ))}
            </Map>
          </YMaps>
        )}
        <Box
          padding={10}
          display="flex"
          flexDirection="column"
          alignItems="center"
        >
          <img src={book} alt="Первый раз в волонтерстве?" width={160} />
          <Typography variant="h4">Первый раз в волонтерстве?</Typography>
          <Typography>
            Давайте расскажем как это работает и подберем задания, которые вам
            подойдут
          </Typography>
          <Link to="/onboarding">
            <Button sx={{ marginTop: 2 }} variant="contained">
              Пройти опрос
            </Button>
          </Link>
          <Typography variant="caption">Займет 3 минуты</Typography>
        </Box>
      </Box>
    </Container>
  );
});

export default Main;
